.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.instagram {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.instaName {
    font-family: serif;
    display: flex;
    justify-content: center;
    font-style: normal;
    font-size: 24px;
    line-height: 32px;
    color: #171717;
}

.photo {
    width: 200px;
    height: 200px;
    transition-duration: 0.6s;
    margin-right: 20px;
}

.photoHeight {
    height: 395px;
    display: flex;
    align-items: center;
}

.footerMenu {
    height: 18px;
    width: 353px;
    margin-bottom: 30px;
}

.footerMenuText {
    font-family: serif;
    text-decoration: none;
    font-style: normal;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}

.circleBtn {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: 1px solid #E5E5E5;
    background-color: white;
    display: flex;
    align-item: center;
    justify-content: center;
}

.footerMedia {
    width: 304px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
}

.footerCompanyName {
    font-family: serif;
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 33px;
    letter-spacing: 2.5px;
    color: #171717;
    text-transform: uppercase;
    margin-bottom: 60px;
}

.footerSignature {
    font-family: serif;
    padding-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footerMenuText li {
    list-style-type: none;
}

.footerMenuText li a {
    cursor: pointer;
    transition-duration: 0.6s;
}

.footerMenuText li a:hover {
    color: #C9A96E;
}