.hero {
    height: 600px;
    display: flex;
    flex-direction: column;
}

.bannerImg {
    width: 100%;
}

.heroText {
    position: absolute;
    margin-top: 350px;
    margin-left: 10%;
    display: flex;
    flex-direction: column;
    align-items: start;
    z-index: 0;
}


.vehicle {
    height: 18px;
    font-family: 'PT Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    color: #FFFFFF;
    z-index: 0;
}

.one {
    text-align: left;
    height: 100px;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 50px;
    color: #FFFFFF;
    width: 500px;
    z-index: 0;
}

.textstr {
    height: 18px;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #FFFFFF;
    z-index: 0;
}


.text {
    display: flex;
    margin: 0px;
    padding: 0px;
    z-index: 0;
}
.text1 {
    margin-right: 10px;
    z-index: 0;
}

.text li a {
    margin-right: 33px;
}