.content {
    margin-top: 60px;
    display: flex;
    justify-content: center;
    margin-bottom: 80px;
}

.posts {
    margin-right: 130px;
}

.post {
    height: 411px;
    width: 370px;
    background-color: #ffffff;
    margin-right: 30px;
    margin-bottom: 15px;
    filter: drop-shadow(0px 2px 6px rgba(0, 0, 0, 0.1));
}

.postsBlock {
    display: flex;
    justify-content: center;
}

.ribbonPanel {
    width: 270px;
    height: 2566px;
    background-color: rgb(255, 255, 255);
}

.ribbonBtn {
    width: 270px;
    height: 50px;
    border: 1px solid #E5E5E5;
    border-radius: 3px;
    background-color: #fff;
    margin-bottom: 20px;
   
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
}

.ribbonBtn:not(:first-child) {
margin-top: 70px;
}

.ribbonMainNews {
    width: 270px;
    height: 385px;
    background-color:rgb(255, 255, 255);
    filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.1));
}

.panelTextHeader {
    text-align: center; 
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #171717;
    margin-top: 20px;
}

.panelTextContent {
font-family: 'PT Serif';
font-style: italic;
font-weight: 400;
font-size: 14px;
line-height: 18px;
margin-top: 4px;
margin-bottom: 14px;
text-align: center;
color: #B1B1B1;
}

.panelTextFooter {
    width: 230px;
    margin-left: auto;
    margin-right: auto;
    font-family: 'PT Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #666666;
}
.panelTextCont {
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: #C9A96E;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    margin-top: 27px;
    margin-bottom: 12px;
}
.postTextblock {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.textblockFooter {    
    display: flex;
    width: 166px;
    justify-content: space-between;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}

.texblockHeader {
    color:#C9A96E;
    text-transform: uppercase;
    margin-bottom: 4px;
}

.textblockContent {
    width: 256px;
    text-align: center;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 8px;
}

.bigPostTextblock {
    margin-left: 31px;
    margin-top: 25px;
    margin-bottom: 25px;
}

.bigTexblockHeader {
     color:#C9A96E;
    text-transform: uppercase;
    margin-bottom: 4px;
}

.bigTextblockTitle {
    width: 550px;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 8px;
}

.bigTextblockFooter {
    display: flex;
    width: 166px;
    justify-content: space-between;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}

.bigTextblockContent {
    width: 614px;
    position: absolute;
    font-family: 'PT Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #666666;
    margin-top: 14px;
}

.bigPost {
    height: 561px;
    width: 770px;
    background-color: #ffffff;
    margin-bottom: 15px;
    filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.1));
}

.category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #666666;
    height: 52px;
}

.category:nth-last-child(n+2) {
    border-bottom: solid 1px #666666;
}

.mediaBlock {
    display: flex;    
    justify-content: space-between;
    margin-bottom: 3px;
}

.mediaBtn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 88px;
    height: 88px;
    border: 0;
    background: #F8F9FA;
}

.mediaBtnText {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #999999;
    margin-top: 6px;
}

.tag {     
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #666666;
    background: #F8F9FA;
    padding: 6px 15px;   
    border: 0;
    margin-bottom: 10px;
    margin-left: 5px;
}

.postPanel {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.panelFooter {    
    display: flex;
    width: 166px;
    justify-content: space-between;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}

.panelHeader {
    font-family: 'PT Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color:#C9A96E;
    text-transform: uppercase;
    margin-bottom: 4px;
}

.panelContent {
    width: 256px;
    text-align: center;
    margin-bottom: 8px;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
}

.ribbonText2 {
    width: 270px;
    height: 287px;
    background-color:rgb(255, 255, 255);
    filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.1));
}

.ribbonText2:nth-last-child(n+2) {
    margin-bottom: 30px;
}