.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.modalOverlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgb(0, 0, 0, 0.7);
    z-index: 1;
}

.modalWindow {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 70%;
    height: 70%;
    border-radius: 10px;
    background-color: #fff;
    z-index: 2;
}

.modalHeader {
    width: 95%;
    display: flex;
    justify-content: center;
    padding: 10px;
    border-bottom: 1px solid #E5E5E5;
    font-family: 'PT Sans', serif;
    font-size: 1.2vw;
    line-height: 1.7vw;
    text-transform: uppercase;
}

.modalFooter {
    width: 100%;
    background-color: #61dafb;
    display: flex;
    justify-content: center;
}

.modalClose {
    font-size: 1.7vw;
    cursor: pointer;
}

.modalBody {
    padding: 0.3vw 0.6vw;
    display: flex;
    justify-content: center;
    width: 50%;
    margin-bottom: 1.3vw;
    max-height: 45vw;
    overflow: auto;
}

.todoInput {
    display: flex;
    justify-content: center;
    align-items: center;
}

.marginRight {
    margin-right: 20px;
}

.input {
    margin-right: 20px;
    text-align: center;
    padding: 10px 0px;
}

.modalBtn {
    width: 90px;
    margin-right: 20px;
    border-radius: 3px;
    border: 1px solid #E5E5E5;
    background-color: #dfdcbe;
    cursor: pointer;
}